html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  margin: 0;
  overflow: overlay;
  font-family: "PT Mono", "Courier New", monospace;
  color: #666666; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.btn {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-align: center;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease; }

